:root {
  --primary: #fbcfe8;
  --secondary: #d8b4fe;
  --text-dark: #374151;
  --bg: #fff0f6;
  --card-bg: #ffffff;
  --card-border: #e5e7eb;
  --link-color: #9333ea;
  --link-hover: #7c3aed;
}

body.dark, .dark-mode {
  --primary: #7c3aed;
  --secondary: #6d28d9;
  --text-dark: #e5e7eb;
  --bg: #111827;
  --card-bg: #1f2937;
  --card-border: #374151;
  --link-color: #c084fc;
  --link-hover: #e9d5ff;
  background-color: var(--bg);
  color: var(--text-dark);
}

html, body, #root {
  margin: 0;
  padding: 0;
  background-color: var(--bg);
  color: var(--text-dark);
  font-family: 'Poppins', sans-serif;
  transition: background-color 0.3s ease, color 0.3s ease;
  scroll-behavior: smooth;
}

.App {
  max-width: 900px;
  margin: auto;
  padding: 2rem;
  box-sizing: border-box;
}

/* Navigation Bar */
.nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--bg);
  border-bottom: 1px solid var(--card-border);
  padding: 1rem 2rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  z-index: 1000;
  backdrop-filter: blur(10px);
}

.nav a {
  text-decoration: none;
  color: var(--link-color);
  font-weight: 600;
  font-size: 1rem;
  transition: color 0.2s ease;
}

.nav a:hover {
  color: var(--link-hover);
}

/* Hero Section */
.hero {
  background: linear-gradient(to right, var(--primary), var(--secondary));
  padding: 4rem 2rem 3rem;
  border-radius: 1.5rem;
  text-align: center;
  margin-top: 5rem;
}

.hero h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

/* Sections */
section {
  padding: 4rem 2rem;
  box-sizing: border-box;
}

.quick-bio ul {
  list-style: none;
  padding-left: 0;
  font-size: 1rem;
  line-height: 1.8;
}

/* Project Cards */
.project-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 2rem;
}

.project-card {
  width: 250px;
  min-height: 180px;
  padding: 1rem;
  background-color: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 1rem;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.05);
  text-align: center;
  transition: all 0.3s ease;
}

.project-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.08);
}

/* Contact Section */
.contact a {
  color: var(--link-color);
  text-decoration: none;
  font-weight: 500;
}

.contact a:hover {
  text-decoration: underline;
  color: var(--link-hover);
}

/* Easter Egg Animation */
.easter-egg {
  color: gold;
  font-weight: 600;
  font-size: 1.1rem;
  animation: popIn 0.5s ease;
}

@keyframes popIn {
  0% { opacity: 0; transform: scale(0.8); }
  100% { opacity: 1; transform: scale(1); }
}

/* Tooltip */
.tooltip {
  position: relative;
  cursor: pointer;
  display: inline-block;
}

.tooltiptext {
  visibility: hidden;
  background-color: #fdf6f0;
  color: #333;
  text-align: center;
  border-radius: 8px;
  padding: 0.5rem;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.75rem;
  white-space: nowrap;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

/* Verse Blockquote */
blockquote {
  font-style: italic;
  font-size: 1.05rem;
  line-height: 1.6;
  margin: 1.5rem 0;
  color: var(--text-dark);
}
/* Mood Tracker */
.mood-tracker {
  padding: 3rem 2rem;
  text-align: center;
}

.emoji-row {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin: 1.5rem 0;
}

.emoji-btn {
  font-size: 2rem;
  background: transparent;
  border: 2px solid var(--card-border);
  border-radius: 50%;
  width: 58px;
  height: 58px;
  cursor: pointer;
  transition: background 0.25s ease, transform 0.25s ease;
}

.emoji-btn:hover:not(:disabled) {
  background: var(--primary);
  transform: translateY(-3px);
}

.emoji-btn.chosen {
  background: var(--secondary);
  cursor: default;
  transform: none;
}

.mood-msg {
  font-size: 0.95rem;
  margin-top: 0.5rem;
  opacity: 0.8;
}

.mood-streak {
  font-weight: 600;
  margin-top: 0.4rem;
  color: var(--link-color);
}
/* Bunny hop animation */
@keyframes hop {
  0%, 100% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-10px);
  }
}

.secret-reveal img {
  animation: hop 1s infinite ease-in-out;
}
.theme-light {
  background: #fffdf9;
  color: #111;
}

.theme-sunset {
  background: linear-gradient(to bottom, #ffd1dc, #ffb347);
  color: #222;
}

.theme-night {
  background: #0f172a;
  color: #f0f0f0;
}
.vibe {
  margin: 2rem auto;
  max-width: 800px;
  padding: 1rem;
  text-align: center;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  height: 0;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}
.vibe {
  padding: 1.5rem;
  background-color: #fef9f9;
  border-radius: 12px;
  margin: 1rem 0;
}

.vibe-play-btn {
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 0.75rem 1.25rem;
  font-size: 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.vibe-play-btn:hover {
  background-color: #f3f4f6;
}
